<template>
    <SubModal v-model:isVisible="modalVisible">
      <div class="modal-close" @click="closeCreate"><img alt="" src="../assets/icons/cancel.png"></div>
      <div class="modal__content-title">Пожалуйста выберите:</div>
      <DatabaseObject v-for="(object, index) in databaseObjects"
        :key="index" 
        :objects="object.values"
        :title="object.title"
        @update:selected="object.selected = $event" 
        @update:objects="object.values = $event"/>
    </SubModal>
</template>

<script>
import DatabaseObject from './DatabaseCreate/DatabaseObject.vue';
import DatabaseObjectApi from '../api/DatabaseObjectApi';
import UploadApi from "@/api/UploadApi";

export default {
    name: 'affairs-create',
    props: {
      show: Boolean,
    },
    components: { DatabaseObject },
    emits: ['update:isVisible'],
    data() {
      return {
        modalVisible: false,
        databaseObjects: {
          name: {
            title: 'Название объекта',
            values: [
              { action: 'text' },
            ],
            selected: ''
          },
          trademark: {
            title: 'Товарный знак',
            values: [
              { name: 'файл', action:'file', options: false },
              { name: 'ссылка', action:'text', options: false }
            ],
            selected: ''
          },
          territory: {
            title: 'Территория',
            values: [
              { name: 'Казахстан (KZ)' },
            ],
            selected: ''
          },
          nonProtectedElements: {
            title: 'Неохраноспособные элементы',
            values: [
              { action: 'text' },
            ],
            selected: ''
          },
          client: {
            title: 'Наименование и адрес владельца',
            values: [
              { action: 'text' },
            ],
            selected: ''
          },
          numberOrder: {
            title: 'Номер заявки',
            values: [
              { action: 'text' },
            ],
            selected: ''
          },
          dateOrder: {
            title: 'Дата заявки',
            values: [
              { action: 'date', options: false },
            ],
            selected: ''
          },
          numberRegistration: {
            title: 'Номер регистрации',
            values: [
              { action: 'text' },
            ],
            selected: ''
          },
          dateRegistration: {
            title: 'Дата регистрации',
            values: [
              { action: 'date', options: false },
            ],
            selected: ''
          },
          datePublication: {
            title: 'Дата публикации',
            values: [
              { action: 'date', options: false },
            ],
            selected: ''
          },
          priorityNumber: {
            title: 'Номер приоритета',
            values: [
              { action: 'text' },
            ],
            selected: ''
          },
          priorityDate1: {
            title: 'Дата приоритета',
            values: [
              { action: 'date', options: false },
            ],
            selected: ''
          },
          priorityDate2: {
            title: 'Дата приоритета',
            values: [
              { action: 'date', options: false },
            ],
            selected: ''
          },
          validityStart: {
            title: 'Начало срока действия',
            values: [
              { action: 'date', options: false },
            ],
            selected: ''
          },
          validityEnd: {
            title: 'Конец срока действия',
            values: [
              { action: 'date', options: false },
            ],
            selected: ''
          },
          extensionStart: {
            title: 'начало срока продления',
            values: [
              { action: 'date', options: false },
            ],
            selected: ''
          },
          extensionEnd: {
            title: 'нконец срока продления',
            values: [
              { action: 'date', options: false },
            ],
            selected: ''
          },
          niceClasses: {
            title: 'Классы МКТУ',
            values: [
              { action: 'text' },
            ],
            selected: ''
          },
        }
      }
    },
    watch: {
      show(newVal) {
        this.modalVisible = newVal;
      },
      modalVisible(newVal) {
        this.$emit('update:show', newVal);
      },
      async allSelectedFilled(newVal) {
        if(newVal) {
          if(newVal.data.trademark.name.split('.').length > 1) {
            newVal.data.trademark = (await UploadApi.upload(newVal.data.trademark)).data.fileName;
          }
          const object = await DatabaseObjectApi.create(newVal.data);
          if(object.data.id !== undefined) {
            this.$emit('update:objectSelect', object.data);
          }
        }
      },
    },
    computed: {
      allSelectedFilled() {
        const selected = {};
        for(const object of Object.entries(this.databaseObjects)) {
          if(!object[1].selected && object[1].selected === '' && !object[1].selected.name) {
            return false;
          }
          if(object[1].selected.name.split('.').length > 1) {
            selected[object[0]] = object[1].selected;
          } else {
            selected[object[0]] = object[1].selected.name
          }
        }
        return {isValid: true, data: selected };
      }
    },
    methods: {
      closeCreate() {
        this.modalVisible = false;
      },
    },
    mounted() {
      this.modalVisible = this.show;
    },
  }
</script>   