<template>
  <div class="toggle">
    <div
        v-for="(item, index) in items"
        :key="index"
        :class="{'toggle__item': true, 'toogle-filter': isFilter, 'active': `${item}_${index}` === toggleId}"
        @click="select(`${item}_${index}`, item)"
    >
      <div v-if="!isImg">{{ item }}</div>
      <img v-if="isImg" :src="item.src"/>
    </div>
  </div>
</template>

<script>
export default {
  name: 'toggle-button',
  props: {
    items: {
      type: Array,
      required: true,
    },
    isImg: {
      type: Boolean,
      default: false,
    },
    isFilter: {
      type: Boolean,
      default: false,
    },
    allowMultiple: { // Добавляем новый prop
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      toggleId: this.allowMultiple ? [`${this.items[0]}_0`] : `${this.items[0]}_0`,
    }
  },
  methods: {
    select(id, item) {
      if (this.allowMultiple) {
        const index = this.toggleId.indexOf(id);
        if (index > -1) {
          this.toggleId.splice(index, 1);
        } else {
          this.toggleId.push(id);
        }
      } else {
        this.toggleId = id;
      }
      console.log(id, item);
      this.$emit('item-selected', this.allowMultiple ? this.toggleId : item);
    },
  }
}

</script>
  